:root {
  --primary: #fff;
}

.btn {
  padding: 8px 20px;
  border-radius: 4px;
  outline: none;
  cursor: pointer;
  white-space: nowrap;
}

.btn--primary {
  background-color: var(--primary);
  color: #4b59f7;
  border: 1px solid var(--primary);
}

.btn--outline {
  background-color: transparent;
  color: #fff;
  border: 1px solid var(--primary);
  transition: all 0.3s ease-out;
}

.btn--medium {
  padding: 8px 20px;
  font-size: 18px;
}

.btn--large {
  padding: 12px 26px;
  font-size: 20px;
}

.btn--wide {
  padding: 12px 40px;
  font-size: 20px;
}

.btn--mobile {
  text-align: center;
  border-radius: 4px;
  width: 60%;
  text-decoration: none;
  font-size: 24px;
  background-color: transparent;
  color: #fff;
  padding: 14px 20px;
  border: 1px solid #fff;
  transition: all 0.3s ease-out;
}

.btn--medium:hover,
.btn--wide:hover,
.btn--large:hover,
.btn--mobile:hover {
  color: #fff;
  background-color: #0467fb;
  transition: all 0.2s ease-out;
}

.blue {
  background-color: #276afb;
  color: #fff;
  border: none;
}

.red {
  background-color: #f00946;
  color: #fff;
  border: none;
}
.red:hover {
  background-color: #276afb;
  color: #fff;
}

.primary {
  background-color: #242424;
  color: #fff;
  border: none;
}
.primary:hover {
  background-color: #fff;
  color: #242424;
  border: none;
}

.green {
  background-color: #25ce4a;
  color: #fff;
  border: none;
}

.green:hover {
  background-color: #242422;
  color: #fff;
  border: none;
}

.btn-link {
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  padding: 8px 16px;
  height: 100%;
  width: 100%;
}
